
.app-wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;

  grid-template-areas:
    "h h"
    "n c"
  ;
}

.app-wrapper-content {
  grid-area: c;
  background-color: rgb(88, 88, 88);

}

